@import url("https://fonts.googleapis.com/css2?family=Orbitron:wght@800&family=Poppins:wght@400;600;700&display=swap");
@import "./sass/homepage";
@import "./sass/login-register";
@import "./sass/dashboard";
@import "./sass/message";
@import "./sass/media-queries";

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container {
    margin: 0 auto;
    padding: 0 1rem 0 1rem;
}

.nav-bar {
    .container {
        .burger-menu-btn {
            display: none;
            padding: 0.5rem;
            border-radius: 5px;
            i {
                font-weight: 700;
                font-size: 2rem;
                color: white;
            }
        }
    }
}

.mobile-nav {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 99999;
    height: 100%;
    background-color: rgba(0, 0, 0, 1);
    color: white;
    display: flex;
    align-items: center;
    flex-direction: column;
    overflow: hidden;
    transition: 0.2s width ease;
    .close-button-wrap {
        padding: 2rem;
        display: flex;
        width: 100%;
        justify-content: flex-end;
        i {
            font-size: 25px;
        }
    }
    .choices {
        display: flex;
        align-items: center;
        flex-direction: column;
        span {
            margin-top: 2rem;
            a {
                color: white;
                text-decoration: none;
                font-family: "Poppins", sans-serif;
                font-size: 25px;
            }
        }
    }
    img {
        width: 70px;
        margin-top: 6rem;
    }
}
